import React, { useState, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';

function Clients() {
    const navigate = useNavigate();
    const [clients, setClients] = useState([]);

    useEffect(() => {
        getClients();
    }, []);

    const getClients = async() => {
        const data = await fetch('http://localhost:3090/clients');
        const clients = await data.json();
        console.log(clients);
        setClients([...clients]);
    };

    const addClient = () => {
        navigate('add', { state: { name: 'Product Name', category: 'Product Category', product_price: 'Product Price', location: 'Product Location', unitsInStock: 'Units On Stock' } });
    };

    const upDate = (client) => {
        console.log(client);
        navigate('update', { state: client });
    };

    const remove = async (clientId) => {
        console.log(clientId);
        // const data = 
        await fetch('http://localhost:3090/clients/remove', {
            method: 'DELETE',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({ client: clientId })
        });
        const newClients = clients.filter(client => client.id !== clientId);
        setClients([...newClients]);
    };

    return (
        <div>
            <h1>Clients</h1>
            <Link className='navA' to='/osher-ad.co.il/market/administrator'>
                <span>חזרה</span>
            </Link>
            <button onClick={addClient}>add client</button>
            <div className='grid'> {clients.map(client => {
                return (
                    <div className='container' key={client.id}>
                        <h2>{client.firstName}</h2>
                        <p>{client.lastName}</p>
                        {/* <p>מחיר: {client.client_price} ש"ח</p>
                        <p>מס' יחידות במלאי: {client.unitsInStock}</p> */}
                        <button onClick={() => { upDate(client) }}>עדכן</button>
                        <button onClick={() => { remove(client.id) }}>הסרה מרשימת המוצרים</button>
                    </div>
                );
            })}</div>
        </div>
    )
};

export default Clients;
